* {
 box-sizing: border-box;
 margin: 0;
 padding: 0;
}
html {
  	margin: 0;
  	padding: 0;
}
body {
	color: silver;
  margin: 0;
  padding: 0;
  font-family: 'montserrat', sans-serif;
}

h1 {
  color: #118888;
  font-family: 'audiowide', sans-serif;
  font-size: 40px;
  letter-spacing: 3px;
  padding: 50px 0;
}
h2 {
  color: #118888;
  font-family: 'montserrat', sans-serif;
  letter-spacing: 2px;
}
h3 {
  color: #118888;
  font-family: 'montserrat', sans-serif;
  font-weight: 500;
  letter-spacing: 2px;
  margin: 10px auto;
  padding: 0;
}
p {
  font-size: 18px;
  line-height: 1.5;
}

button {
  background: none;
  border: 2px solid #889999;
  border-radius: 4px;
  color: #889999;
  cursor: pointer;
  font-family: "montserrat";
  font-size: 20px;
  letter-spacing: 1px;
  margin: 5px;
  padding: 3px 6px;
  pointer-events: auto;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  white-space: nowrap;
}
button:hover {
  border: 2px solid #226666;
  color: #339999;
  letter-spacing: 2px;
}
a {
  text-decoration: none;
}

@media (max-width: 1000px) {
  p {
    font-size: 16px;
  }
  button {
    font-size: 18px;
  }
}

.App {
  background: -webkit-linear-gradient(bottom, #111, #122);
  background: linear-gradient(to top, #111, #122);
  min-height: 100vh;
  overflow: hidden;
  text-align: center;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.half-screen {
  display: -webkit-flex;
  display: flex;
  width: 50%;
}

.flex-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.flex-container * {
  margin: auto;
}

.section-bg {
  position: relative;
  z-index: 2;
}
.toAppear {
  overflow: hidden;
  opacity: 0;
  position: relative;
  -webkit-transform: scale(0.96);
          transform: scale(0.96);
  -webkit-transition: all 0.6s ease;
  transition: all 0.6s ease;
}
.appear {   /*  animation  */
  opacity: 1 !important;
  -webkit-transform: scale(1) !important;
          transform: scale(1) !important;
}
.appearOnLoad {
  overflow: hidden;
  opacity: 0;
  position: relative;
  -webkit-transform: scale(0.96);
          transform: scale(0.96);
  -webkit-transition: all 1.6s ease;
  transition: all 1.6s ease;
}


.noscroll {
  overflow: hidden;
}

@media (max-width: 800px) {
  .section {
    padding: 0;
  }
}


.FrontPage {
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  opacity: 1;
}
.FrontPage-hidden {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  opacity: 0;
}
