@media (max-width: 1000px) {
	.overlay {
		display: inline-block;
		padding: 50px 30px;
	}
	.overlay-container {
		box-sizing: border-box;
		height: auto;
		width: 100%;
	}
	.overlay-container p {
		text-align: left;
	}
}
@media (max-width: 600px) {
	.overlay {
		padding: 50px 20px;
	}
}

.parallax {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  -index: -1;

  height: 100vh;
  width: 100vw;
}
#c {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}
